.page-footer {
  margin: 26px auto 23px auto;
  width: 320px;
  display: flex;
  flex-direction: column;
}

@media (min-width: @tablet-width) {
  .page-footer {
    margin: 36px auto 0 auto;
    width: 663px;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    width: 974px;
  }
}

.page-footer__hashtag {
  padding: 12px 0 36px 0;
}

@media (min-width: @tablet-width) {
  .page-footer__hashtag {
    padding: 12px 0 36px 8px;
  }
}

@media (min-width: @desktop-width) {
  .page-footer__hashtag {
    padding-left: 6px;
  }
}

.page-footer__hashtag-text {
  font-size: 21px;
}

.page-footer__social {
  padding-bottom: 35px;
}

.page-footer__ha {
  margin: 0 auto;
  width: 143px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media (min-width: @tablet-width) {
  .page-footer__ha {
    margin: 0;
  }
}

.page-footer__ha-text {
  margin: 0 0 0 6px;
  padding-top: 11px;
}

@media (min-width: @tablet-width) {
  .page-footer__ha-text {
    margin-left: 8px;
    padding-top: 13px;
  }
}
