.main-nav {
  position: relative;
  margin: 0 auto;
  max-width: 1200px;
  background-color: #ffffff;
  font-size: 18px;
  line-height: 28px;
  font-weight: 700;
}

@media (min-width: @tablet-width) {
  .main-nav {
    font-size: 14px;
  }
}

.main-nav-inactive {
  visibility: hidden;
  position: absolute;
}

.main-nav-active {
  visibility: visible;
  position: relative;
}
